


/* .boat-lifestyle-product-card variant starts */
.card.boat-lifestyle-product-card{
    @apply rounded-[10px] bg-[#FAFAFA] overflow-hidden relative shadow-lg;

}

.card.boat-lifestyle-product-card .imageWrapper{
    @apply   relative;
    /* aspect-ratio: 1;
    isolation: isolate; */
}

.card.boat-lifestyle-product-card .imageWrapper .imageContainer{
    @apply block max-w-[320px];
    aspect-ratio: 1;
}
.card.boat-lifestyle-product-card .imageWrapper .tag{
    @apply absolute top-2 bg-black text-white text-10 rounded-r;
    padding: 4px 8px 5px 3px;
}

.card.boat-lifestyle-product-card .imageWrapper .label{
    @apply absolute bottom-[-.55rem] rounded-xl text-12 leading-none font-bold text-center py-[6px] px-[10px] left-[15%] w-[70%] bg-[#fcc50b];
}

.card.boat-lifestyle-product-card .imageWrapper .productImage{
    /* @apply ; */
    width: 100%;
    height: 100%;
}

.card.boat-lifestyle-product-card .details{
    @apply pt-6 pb-3 px-3;
}

.card.boat-lifestyle-product-card .details .productName{
    @apply text-16 font-bold;
}

.card.boat-lifestyle-product-card .details .colors .colorOption{
    @apply inline-block w-[20px] h-[20px] rounded-[50%];
    vertical-align: middle;
}

.card.boat-lifestyle-product-card .details .colors .remaingColorsCount{
    @apply text-14 text-[#4e5358];
    vertical-align: middle;
}

.card.boat-lifestyle-product-card .details .price .discountPrice{
    @apply inline-block text-16 font-bold mr-1 leading-none;
}
.card.boat-lifestyle-product-card .details .price .actualPrice{
    @apply inline-block text-12 font-bold mr-1 text-[#b2b9bf] line-through leading-none;
}
.card.boat-lifestyle-product-card .details .price .discountPercentage{
    @apply inline-block text-12 font-bold  text-[#12b985] leading-none;
}

.card.boat-lifestyle-product-card .details .ratingsAndReviews{
    @apply flex items-center gap-x-1 mt-2;
}

.card.boat-lifestyle-product-card .details .ratingsAndReviews .ratings, .card.boat-lifestyle-product-card .details .ratingsAndReviews .reviews{
    @apply text-12 leading-none;
}

.card.boat-lifestyle-product-card .details .ratingsAndReviews .reviews{
    @apply border-l-[1px] border-l-[#b9b9b9] pl-1;
}

.card.boat-lifestyle-product-card .buttons .button{
    @apply block p-3 bg-black text-white rounded-xl text-12 leading-none font-semibold;
}

/* .boat-lifestyle-product-card variant ends */